.event-card {
  box-shadow: rgba(255, 255, 255, 0.1) 0px 1px 1px 0px inset,
    rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
  border: none;
  border-radius: 10%;
  position: relative;
}
.event-type {
  font-size: 12px;
}
.event-time {
  font-size: 10px;
}
.date-card {
  width: 40px;
  margin-top: -40px;
  margin-left: 85%;
  background-color: #c2c2c3;
}
.date-body {
  text-align: left;
  padding: 0.3rem 0.3rem;
}
.date {
  margin-top: -5px;
  margin-bottom: 0;
  font-weight: 900;
}
.month {
  font-weight: 300;
}
